"use client"

import { useState, useEffect, useCallback } from "react"
import { ChevronLeft, ChevronRight } from "lucide-react"
import Image from "next/image"
import { Button } from "@/components/ui/button"

interface Slide {
  title: string
  button: string
  src: string
}

interface Carousel3DProps {
  slides: Slide[]
}

export default function Carousel3D({ slides }: Carousel3DProps) {
  const [currentSlide, setCurrentSlide] = useState(0)
  const [isAnimating, setIsAnimating] = useState(false)
  const totalSlides = slides.length

  const nextSlide = useCallback(() => {
    if (isAnimating) {
      console.log("Next slide blocked: isAnimating true")
      return
    }
    console.log("Next slide triggered")
    setIsAnimating(true)
    setCurrentSlide((prev) => (prev === totalSlides - 1 ? 0 : prev + 1))
  }, [isAnimating, totalSlides])

  const prevSlide = useCallback(() => {
    if (isAnimating) {
      console.log("Prev slide blocked: isAnimating true")
      return
    }
    console.log("Prev slide triggered")
    setIsAnimating(true)
    setCurrentSlide((prev) => (prev === 0 ? totalSlides - 1 : prev - 1))
  }, [isAnimating, totalSlides])

  // Reset isAnimating after transition
  useEffect(() => {
    if (isAnimating) {
      const timeout = setTimeout(() => {
        setIsAnimating(false)
        console.log("isAnimating reset to false")
      }, 500)
      return () => clearTimeout(timeout)
    }
  }, [isAnimating])

  const getSlideStyle = (index: number) => {
    let translateX = 0
    let opacity = 0
    let zIndex = 0

    if (index === currentSlide) {
      translateX = 0
      opacity = 1
      zIndex = 10
    } else if (index === (currentSlide + 1) % totalSlides) {
      translateX = 100
      opacity = 0
      zIndex = 5
    } else if (index === (currentSlide - 1 + totalSlides) % totalSlides) {
      translateX = -100
      opacity = 0
      zIndex = 5
    } else {
      translateX = 100
      opacity = 0
      zIndex = 0
    }

    return {
      transform: `translateX(${translateX}%)`,
      opacity,
      zIndex,
      transition: isAnimating ? "transform 0.5s ease-in-out, opacity 0.5s ease-in-out" : "none",
    }
  }

  return (
    <div className="relative h-[60vh] min-h-[400px] max-h-[600px] overflow-hidden bg-gradient-to-r from-gray-900 to-gray-800">
      <div className="relative w-full h-full">
        {slides.map((slide, index) => (
          <div
            key={index}
            className="absolute inset-0 w-full h-full flex items-center justify-center"
            style={getSlideStyle(index)}
          >
            <div className="relative w-[80%] h-[80%] rounded-lg overflow-hidden shadow-2xl flex flex-col items-center justify-end bg-black/50">
              <Image
                src={slide.src || "/placeholder.svg"}
                alt={slide.title}
                className="absolute inset-0 w-full h-full object-cover transition-transform duration-500 hover:scale-105"
                width={1200}
                height={600}
                priority={index === 0}
                onError={() => console.error(`Failed to load image: ${slide.src}`)}
              />
              <div className="relative z-10 text-white text-center p-4">
                <h2 className="text-lg md:text-2xl lg:text-3xl font-semibold mb-2">{slide.title}</h2>
                <Button
                  variant="outline"
                  className="bg-white/20 text-white hover:bg-white/30"
                >
                  {slide.button}
                </Button>
              </div>
            </div>
          </div>
        ))}
      </div>

      <div className="absolute inset-0 flex items-center justify-between p-4 z-30 pointer-events-none">
        <Button
          variant="ghost"
          size="icon"
          onClick={(e) => {
            e.stopPropagation()
            prevSlide()
          }}
          className="h-12 w-12 rounded-full bg-black/50 text-white hover:bg-black/70 pointer-events-auto"
          aria-label="Previous slide"
        >
          <ChevronLeft className="h-6 w-6" />
        </Button>
        <Button
          variant="ghost"
          size="icon"
          onClick={(e) => {
            e.stopPropagation()
            nextSlide()
          }}
          className="h-12 w-12 rounded-full bg-black/50 text-white hover:bg-black/70 pointer-events-auto"
          aria-label="Next slide"
        >
          <ChevronRight className="h-6 w-6" />
        </Button>
      </div>

      <div className="absolute bottom-4 left-0 right-0 z-30 flex justify-center gap-2 pointer-events-none">
        {slides.map((_, index) => (
          <button
            key={index}
            className={`h-2 rounded-full transition-all pointer-events-auto ${
              currentSlide === index ? "bg-white w-6" : "bg-white/50 w-2"
            }`}
            onClick={(e) => {
              e.stopPropagation()
              if (!isAnimating) setCurrentSlide(index)
            }}
            aria-label={`Go to slide ${index + 1}`}
          />
        ))}
      </div>
    </div>
  )
}